import React, { FC, useRef, DragEvent } from 'react'
import '../Slider.scss'
import { SlideProps } from '../types/interfaces'
import { changeElementStyle } from '../../../../utils'

const Slide: FC<SlideProps> = ({
  child,
  width,
  height,
  scaleOnDrag = false,
}) => {
  const slideRef = useRef<HTMLDivElement>(null)

  const onMouseDown = (): void => {
    if (scaleOnDrag)
      changeElementStyle(slideRef.current!, { transform: 'scale(.9)' })
  }

  const onMouseUp = (): void => {
    if (scaleOnDrag)
      changeElementStyle(slideRef.current!, { transform: 'scale(1)' })
  }

  const dragStartHandler = (e: DragEvent): void => {
    e.preventDefault()
    e.stopPropagation()
  }

  return (
    <div ref={slideRef} className='Slide' style={{ width, height }}>
      <div
        className='Slide__Inner'
        onDragStart={dragStartHandler}
        onMouseDown={onMouseDown}
        onMouseLeave={onMouseUp}
        onMouseUp={onMouseUp}
        onTouchEnd={onMouseUp}
        onTouchStart={onMouseDown}
        unselectable='on'
      >
        {child}
      </div>
    </div>
  )
}

export default Slide
